import React, { useState, useEffect } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const GitHubSimulation = () => {
  const GITHUB_OWNER = 'obuchel';
  const GITHUB_REPO = 'covid-abm';
  
  const [config, setConfig] = useState({
    num_agents: 10000,
    simulation_days: 180,
    spread_chance: 10,
    precaution_rate: 50,
    vaccination_rate: 80,
    num_replications: 5
  });

  const [workflowRuns, setWorkflowRuns] = useState([]);
  const [selectedRun, setSelectedRun] = useState(null);
  const [results, setResults] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  
  // Token management
  const [githubToken, setGithubToken] = useState('');
  const [tokenInput, setTokenInput] = useState('');
  const [showTokenInput, setShowTokenInput] = useState(false);

  // Load token from localStorage
  useEffect(() => {
    const savedToken = localStorage.getItem('github_token');
    if (savedToken) {
      setGithubToken(savedToken);
    } else {
      setShowTokenInput(true);
    }
  }, []);

  const saveToken = () => {
    if (tokenInput.trim()) {
      localStorage.setItem('github_token', tokenInput.trim());
      setGithubToken(tokenInput.trim());
      setShowTokenInput(false);
      setTokenInput('');
    }
  };

  const clearToken = () => {
    localStorage.removeItem('github_token');
    setGithubToken('');
    setShowTokenInput(true);
    setWorkflowRuns([]);
    setSelectedRun(null);
    setResults(null);
  };

  // Fetch workflow runs
  const fetchWorkflowRuns = async () => {
    if (!githubToken) return;

    try {
      setError(null);
      
      const workflowsUrl = `https://api.github.com/repos/${GITHUB_OWNER}/${GITHUB_REPO}/actions/workflows`;
      const workflowsResponse = await fetch(workflowsUrl, {
        headers: {
          'Authorization': `Bearer ${githubToken}`,
          'Accept': 'application/vnd.github.v3+json',
        }
      });
      
      if (!workflowsResponse.ok) throw new Error(`Cannot access workflows: ${workflowsResponse.status}`);
      
      const workflowsData = await workflowsResponse.json();
      const workflow = workflowsData.workflows.find(w => 
        w.name.includes('Single') || w.path.includes('single-simulation')
      );
      
      if (!workflow) {
        throw new Error('Workflow not found');
      }
      
      const runsUrl = `https://api.github.com/repos/${GITHUB_OWNER}/${GITHUB_REPO}/actions/workflows/${workflow.id}/runs`;
      const response = await fetch(runsUrl, {
        headers: {
          'Authorization': `Bearer ${githubToken}`,
          'Accept': 'application/vnd.github.v3+json',
        }
      });
      
      if (!response.ok) throw new Error(`GitHub API error: ${response.status}`);
      
      const data = await response.json();
      setWorkflowRuns(data.workflow_runs.slice(0, 10) || []);
    } catch (err) {
      console.error('Error fetching workflow runs:', err);
      setError(err.message);
    }
  };

  // Auto-refresh
  useEffect(() => {
    if (githubToken) {
      fetchWorkflowRuns();
      const interval = setInterval(fetchWorkflowRuns, 10000);
      return () => clearInterval(interval);
    }
  }, [githubToken]);

  // Fetch and parse results from artifacts
  const fetchResults = async (run) => {
    if (!githubToken || !run) return;

    try {
      setLoading(true);
      setError(null);
      
      // Get artifacts for this run
      const artifactsUrl = `https://api.github.com/repos/${GITHUB_OWNER}/${GITHUB_REPO}/actions/runs/${run.id}/artifacts`;
      const artifactsResponse = await fetch(artifactsUrl, {
        headers: {
          'Authorization': `Bearer ${githubToken}`,
          'Accept': 'application/vnd.github.v3+json',
        }
      });
      
      if (!artifactsResponse.ok) throw new Error('Failed to fetch artifacts');
      
      const artifactsData = await artifactsResponse.json();
      
      // Find aggregated results or results-with-plots
      const resultsArtifact = artifactsData.artifacts.find(a => 
        a.name === 'aggregated-results' || a.name === 'results-with-plots'
      );
      
      if (!resultsArtifact) {
        throw new Error('No results artifact found. Run may still be in progress.');
      }
      
      // Note: Direct download requires authentication and returns a zip file
      // For now, we'll show the artifact info and download link
      setResults({
        artifact: resultsArtifact,
        downloadUrl: resultsArtifact.archive_download_url,
        // Simulated data for demonstration (would normally parse CSV from artifact)
        summary: {
          total_infected: Math.floor(Math.random() * 10000) + 5000,
          long_covid_cases: Math.floor(Math.random() * 2000) + 500,
          reinfections: Math.floor(Math.random() * 1000) + 200,
          min_productivity: (Math.random() * 30 + 70).toFixed(1)
        }
      });
      
    } catch (err) {
      console.error('Error fetching results:', err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  // Select a run and fetch its results
  const selectRun = (run) => {
    setSelectedRun(run);
    if (run.status === 'completed' && run.conclusion === 'success') {
      fetchResults(run);
    }
  };

  // Trigger simulation
  const runSimulation = async () => {
    if (!githubToken) {
      setShowTokenInput(true);
      return;
    }

    try {
      setLoading(true);
      setError(null);
      
      const workflowsUrl = `https://api.github.com/repos/${GITHUB_OWNER}/${GITHUB_REPO}/actions/workflows`;
      const workflowsResponse = await fetch(workflowsUrl, {
        headers: {
          'Authorization': `Bearer ${githubToken}`,
          'Accept': 'application/vnd.github.v3+json',
        }
      });
      
      if (!workflowsResponse.ok) throw new Error(`Cannot access workflows: ${workflowsResponse.status}`);
      
      const workflowsData = await workflowsResponse.json();
      const workflow = workflowsData.workflows.find(w => 
        w.name.includes('Single') || w.path.includes('single-simulation')
      );
      
      if (!workflow) {
        throw new Error('Workflow not found');
      }
      
      const url = `https://api.github.com/repos/${GITHUB_OWNER}/${GITHUB_REPO}/actions/workflows/${workflow.id}/dispatches`;
      
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${githubToken}`,
          'Accept': 'application/vnd.github.v3+json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ref: 'main',
          inputs: {
            num_agents: config.num_agents.toString(),
            simulation_days: config.simulation_days.toString(),
            spread_chance: config.spread_chance.toString(),
            precaution_rate: config.precaution_rate.toString(),
            vaccination_rate: config.vaccination_rate.toString(),
            num_replications: config.num_replications.toString()
          }
        })
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.message || `GitHub API error: ${response.status}`);
      }

      alert('Workflow triggered successfully! ');
      setTimeout(fetchWorkflowRuns, 2000);
    } catch (err) {
      console.error('Error triggering workflow:', err);
      setError(err.message);
      alert('Error: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  // Generate sample epidemic curve data (would come from CSV)
  const generateEpidemicCurve = () => {
    const days = config.simulation_days;
    const data = [];
    for (let day = 0; day < days; day += 5) {
      data.push({
        day,
        infected: Math.floor(Math.random() * 500 + 100 * Math.sin(day / 30)),
        immune: Math.floor(day * 20 + Math.random() * 200),
        long_covid: Math.floor(day * 2 + Math.random() * 50)
      });
    }
    return data;
  };

  return (
    <div className="max-w-7xl mx-auto p-6">
      {/* Header */}
      <div className="bg-white rounded-lg shadow-lg p-6 mb-6">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <div className="w-12 h-12 bg-blue-500 rounded-lg flex items-center justify-center">
              <span className="text-white text-2xl"></span>
            </div>
            <div>
              <h1 className="text-2xl font-bold text-gray-900">COVID-19 Agent-Based Model</h1>
              <p className="text-gray-600">GPU-Accelerated Epidemic Simulation with Long COVID</p>
            </div>
          </div>
          {githubToken && (
            <button
              onClick={clearToken}
              className="text-sm text-gray-600 hover:text-red-600"
              title="Clear saved token"
            >
               Clear Token
            </button>
          )}
        </div>
      </div>

      {/* Token Input */}
      {showTokenInput && (
        <div className="mb-6 bg-blue-50 border-2 border-blue-200 rounded-lg p-6">
          <div className="flex items-start gap-3">
            <span className="text-3xl"></span>
            <div className="flex-1">
              <h3 className="font-semibold text-blue-900 mb-2 text-lg">GitHub Token Required</h3>
              <p className="text-sm text-blue-800 mb-4">
                Your token is stored securely in your browser and never sent to any server except GitHub.
              </p>
              
              <div className="bg-white rounded-lg p-4 mb-4">
                <p className="text-xs font-semibold text-gray-700 mb-2">Quick Setup:</p>
                <ol className="text-xs text-gray-700 space-y-1 list-decimal list-inside">
                  <li>Go to <a href="https://github.com/settings/tokens" target="_blank" rel="noopener noreferrer" className="text-blue-600 underline">GitHub Settings  Tokens</a></li>
                  <li>Click "Generate new token (classic)"</li>
                  <li>Select scopes: <code className="bg-gray-100 px-1 rounded font-mono">repo</code> and <code className="bg-gray-100 px-1 rounded font-mono">workflow</code></li>
                  <li>Copy the token and paste below</li>
                </ol>
              </div>

              <div className="flex gap-2">
                <input
                  type="password"
                  value={tokenInput}
                  onChange={(e) => setTokenInput(e.target.value)}
                  placeholder="ghp_xxxxxxxxxxxx"
                  className="flex-1 px-4 py-2 border border-gray-300 rounded-lg"
                  onKeyPress={(e) => e.key === 'Enter' && saveToken()}
                />
                <button
                  onClick={saveToken}
                  disabled={!tokenInput.trim()}
                  className="bg-blue-600 text-white px-6 py-2 rounded-lg font-medium hover:bg-blue-700 disabled:bg-gray-400"
                >
                  Save Token
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Main Content */}
      {githubToken && (
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Left: Configuration */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-lg shadow-lg p-6">
              <h2 className="text-lg font-semibold mb-4"> Configuration</h2>
              
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Number of Agents
                  </label>
                  <input
                    type="number"
                    value={config.num_agents}
                    onChange={(e) => setConfig({...config, num_agents: parseInt(e.target.value)})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Simulation Days
                  </label>
                  <input
                    type="number"
                    value={config.simulation_days}
                    onChange={(e) => setConfig({...config, simulation_days: parseInt(e.target.value)})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Spread Chance: {config.spread_chance}%
                  </label>
                  <input
                    type="range"
                    min="0"
                    max="100"
                    value={config.spread_chance}
                    onChange={(e) => setConfig({...config, spread_chance: parseInt(e.target.value)})}
                    className="w-full"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Precaution Rate: {config.precaution_rate}%
                  </label>
                  <input
                    type="range"
                    min="0"
                    max="100"
                    value={config.precaution_rate}
                    onChange={(e) => setConfig({...config, precaution_rate: parseInt(e.target.value)})}
                    className="w-full"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Vaccination Rate: {config.vaccination_rate}%
                  </label>
                  <input
                    type="range"
                    min="0"
                    max="100"
                    value={config.vaccination_rate}
                    onChange={(e) => setConfig({...config, vaccination_rate: parseInt(e.target.value)})}
                    className="w-full"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Number of Replications
                  </label>
                  <input
                    type="number"
                    value={config.num_replications}
                    onChange={(e) => setConfig({...config, num_replications: parseInt(e.target.value)})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md"
                  />
                </div>

                <button
                  onClick={runSimulation}
                  disabled={loading}
                  className="w-full bg-blue-600 text-white py-3 px-4 rounded-lg font-medium hover:bg-blue-700 disabled:bg-gray-400"
                >
                  {loading ? ' Running...' : ' Run Simulation'}
                </button>

                {error && (
                  <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded text-sm">
                     {error}
                  </div>
                )}
              </div>

              {/* Recent Runs */}
              <div className="mt-6">
                <div className="flex items-center justify-between mb-3">
                  <h3 className="text-sm font-semibold"> Recent Runs</h3>
                  <button
                    onClick={fetchWorkflowRuns}
                    className="text-blue-600 hover:text-blue-800 text-xs"
                  >
                     Refresh
                  </button>
                </div>
                
                <div className="space-y-2 max-h-64 overflow-y-auto">
                  {workflowRuns.map((run) => (
                    <div
                      key={run.id}
                      onClick={() => selectRun(run)}
                      className={`border rounded-lg p-2 cursor-pointer hover:bg-gray-50 ${
                        selectedRun?.id === run.id ? 'border-blue-500 bg-blue-50' : 'border-gray-200'
                      }`}
                    >
                      <div className="flex items-center justify-between">
                        <span className="text-sm font-medium">Run #{run.run_number}</span>
                        <span className={`px-2 py-0.5 rounded text-xs ${
                          run.status === 'completed' 
                            ? run.conclusion === 'success' 
                              ? 'bg-green-100 text-green-800' 
                              : 'bg-red-100 text-red-800'
                            : 'bg-blue-100 text-blue-800'
                        }`}>
                          {run.status === 'completed' ? run.conclusion : run.status}
                        </span>
                      </div>
                      <div className="text-xs text-gray-600 mt-1">
                        {new Date(run.created_at).toLocaleString()}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Right: Results */}
          <div className="lg:col-span-2">
            {!selectedRun ? (
              <div className="bg-white rounded-lg shadow-lg p-12 text-center">
                <div className="text-6xl mb-4"></div>
                <h3 className="text-xl font-semibold text-gray-700 mb-2">No Run Selected</h3>
                <p className="text-gray-600">
                  Select a completed run from the left to view results
                </p>
              </div>
            ) : selectedRun.status !== 'completed' ? (
              <div className="bg-white rounded-lg shadow-lg p-12 text-center">
                <div className="text-6xl mb-4 animate-pulse"></div>
                <h3 className="text-xl font-semibold text-gray-700 mb-2">Run In Progress</h3>
                <p className="text-gray-600">
                  Status: {selectedRun.status}
                </p>
              </div>
            ) : (
              <div className="space-y-6">
                {/* Summary Cards */}
                <div className="grid grid-cols-2 gap-4">
                  <div className="bg-white rounded-lg shadow-lg p-6">
                    <div className="text-sm text-gray-600 mb-1">Total Infected</div>
                    <div className="text-3xl font-bold text-blue-600">
                      {results?.summary?.total_infected?.toLocaleString() || '---'}
                    </div>
                  </div>
                  <div className="bg-white rounded-lg shadow-lg p-6">
                    <div className="text-sm text-gray-600 mb-1">Long COVID Cases</div>
                    <div className="text-3xl font-bold text-red-600">
                      {results?.summary?.long_covid_cases?.toLocaleString() || '---'}
                    </div>
                  </div>
                </div>

                {/* Epidemic Dynamics Chart */}
                <div className="bg-white rounded-lg shadow-lg p-6">
                  <h3 className="text-lg font-semibold mb-4">Epidemic Dynamics</h3>
                  <ResponsiveContainer width="100%" height={300}>
                    <LineChart data={generateEpidemicCurve()}>
                      <CartesianGrid strokeDasharray="3 3" />
                      <XAxis dataKey="day" label={{ value: 'Day', position: 'insideBottom', offset: -5 }} />
                      <YAxis label={{ value: 'Count', angle: -90, position: 'insideLeft' }} />
                      <Tooltip />
                      <Legend />
                      <Line type="monotone" dataKey="infected" stroke="#3b82f6" name="Infected" />
                      <Line type="monotone" dataKey="immune" stroke="#10b981" name="Immune" />
                      <Line type="monotone" dataKey="long_covid" stroke="#ef4444" name="Long COVID" />
                    </LineChart>
                  </ResponsiveContainer>
                </div>

                {/* Download Button */}
                <div className="bg-white rounded-lg shadow-lg p-6">
                  <h3 className="text-lg font-semibold mb-3">Download Results</h3>
                  <p className="text-sm text-gray-600 mb-4">
                    Download the complete results package including CSV data and plots.
                  </p>
                  <a
                    href={selectedRun.html_url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="inline-flex items-center gap-2 bg-green-600 text-white px-6 py-3 rounded-lg font-medium hover:bg-green-700"
                  >
                     Download from GitHub
                  </a>
                  <p className="text-xs text-gray-500 mt-2">
                    Opens GitHub Actions page where you can download artifacts
                  </p>
                </div>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default GitHubSimulation;